<template lang="pug">
    b-container
        h3 List
        b-row
            article-entry.col-auto(v-for="i in list" :key="i.file" :info="i"
                @tagclick="onTagClick"
                :class="{'d-none': !resolve(i)}"
            )
        hr
        h3 Tag
        b-badge.m-1(v-for="i, k in tags" @click.left="onTagClick(k)" :variant="i ? 'info' : ''") {{k}}

</template>

<script>
    import list from '../components/blog/LIST.json'
    import ArticleEntry from "../components/blog/viewComponents/ArticleEntry";

    export default {
        name: "Blog",

        components: {
            ArticleEntry
        },

        data: () => ({
            list,
            tags: {},
        }),

        methods: {
            onTagClick(tag) {
                this.tags[tag] = !this.tags[tag]
            },

            resolve(article) {
                if (this.allFalse) {
                    return true
                }
                for (let i in this.tags) {
                    if (this.tags[i]) {
                        if (!article.confObj.tags.includes(i)) {
                            return false
                        }
                    }
                }
                return true
            },
        },

        computed: {
            allFalse() {
                for (let i in this.tags) {
                    if (this.tags[i]) {
                        return false
                    }
                }
                return true
            }
        },

        async created() {
            const tags = new Set()
            for (let i of list) {
                await import('../components/blog/' + i.conf).then(value => {
                    this.$set(i, 'confObj', value)
                    value.tags.forEach((tag) => {
                        tags.add(tag)
                    })
                })
            }
            tags.forEach(tag => {
                this.$set(this.tags, tag, false)
            })

            if (window.location.search[0] === '?') {
                const s = window.location.search.slice(1)
                this.tags[decodeURI(s)] = true
            }
        },

    }
</script>

